$blue: #228be6; //주석 선언
$gray: #495057;
$pink: #f06595;

@mixin button-color($color) { // 반복되는 코드 mixin기능으로 쉽게 관리가능
    background: $color;
    &:hover{
        background: lighten($color, 10%);
    }
    &:active{
        background: darken($color, 10%);
    }
    &.outline{
        color:$color;
        background: none;
        border: 1px solid $color;
        &:hover{
            background: $color;
            color: white;
        }
    }
}
.Button{
    display: inline-flex;
    color: white;
    font-weight: bold;
    outline: none;
    border-radius: 4px;
    border: none;
    cursor: pointer;

    //사이즈 관리
    &.large{
    padding-top: 13px;
    height: 3rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.25rem;
    }
    &.medium{
    padding-top: 9px;
    height: 2.25rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1rem;
    }
    &.small{
    padding-top: 5px;
    height: 1.75rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.875rem;
    }
    // 색상 관리
    &.blue{
        @include button-color($blue);
    }
    &.gray{
        @include button-color($gray);
    }
    &.pink{
       @include button-color($pink);
    }
    
    &+&{ //.Button + .Button
        margin-left: 1rem;
    }
    &.fullWidth{
        width: 100%;
        justify-content: center;
        &+&{
            margin-left: 0;
            margin-top: 1rem;
        }
    }
}

// .Button {&.large{}}  의미 = .Button.large{}